@import '../common/variables.scss';
.reviews {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.reviews__wrapper {
  width: 87%;
  padding: 50px 0;
}

.reviews__body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews__text {
  color: rgb(40, 54, 69);
  font-size: 14px;
  text-align: center;
  line-height: 2.143;

}

.reviews__author-name {
  color: black;
  font-size: 18px;
  font-weight: bold;
}

.reviews__age {
  display: none;
}

.reviews__wrapper > .owl-carousel > .owl-dots {
  margin-top: 30px;
}




@media (min-width: $tablet-width) {

  .reviews__wrapper {
    width: 94%;
    padding: 3% 0;
  }

  .reviews__age {
    display: block;
    order: 3;
    font-size: 14px;
    line-height: 30px;
    margin-top: -2%;
  }

  .reviews__text {
    font-size: 18px;
  }

  .reviews__author-name {
    font-size: 20px;
    order: 2;
  }
}

@media (min-width: $desktop-width) {
  .reviews {
    position: relative;
    padding: 10% 0;

    &::before {
      content: '';
      position: absolute;
      background-image: url(./../images/quotes.png);
      background-repeat: no-repeat;
      width: 80px;
      height: 80px;
      top: 80px;
    }
  }

  .reviews__wrapper {
    width: 80%;
  }

  .reviews__body {
    width: 70%;
    margin-left: 16%;
  }

  .reviews__author-name {
    font-size: 24px;
  }

  .reviews__age {
    font-size: 18px;
  }
}